  {id: 4, name: '“Доброволец года”', description: 'Доброволец года – это волонтер, имеющий заслуги в добровольческой (волонтерской) деятельности как в качестве участника, так и в качестве организатора (соорганизатора) благотворительных мероприятий. Он не просто сам делает этот мир добрее, но и ведет за собой единомышленников, занимаясь продвижением ценностей добровольческого (волонтерского) движения среди сверстников.', svg: '3.svg' },
  {id: 5, name: '“Молодой профессионал года”', description: 'Молодой профессионал года – это студент, которого с уверенностью можно назвать «мастер своего дела» - будучи еще студентом, он уже проявил себя в выбранной профессиональной сфере – принимал участие и становился призером профессиональных конкурсов, фестивалей и иных мероприятий разного уровня. Горящий своим делом, он занимается популяризацией выбранной профессии среди сверстников.', svg: '4.svg' },
  {id: 6, name: '“Староста года”', description: 'Староста года – это студент, который является не только старостой, но и настоящим лидером своей группы, именно поэтому он и его команда имеют достижения в общественной, спортивной, научной или творческой деятельностях, активно участвуют в жизни вуза/ссуза, города, региона или страны, при этом не забывая про успеваемость в учебе.', svg: '5.png' },
  {id: 7, name: '“Интелллект года”', description: 'Интеллект года – это студент, занимающийся научной деятельностью и имеющий высокие достижения в области науки: он занимается развитием, популяризацией и продвижением научных исследований в вузе и за его пределами, пишет и публикует научные работы или стоит на пороге открытия.', svg: '6.svg' },
  {id: 8, name: '“Патриотическое объединение года”', description: 'Патриотический объединение года – Патриотическое объединение – это объединение студентов, неравнодушных к истории родного города и страны. Проводя поисковые работы по установлению имен погибших и увековечению памяти защитников Отечества, изучая историю и культуру страны и родного региона и постоянно расширяя свои ряды новыми участниками, они участвуют в патриотическом воспитании молодежи', svg: '7.svg' },
  {id: 9, name: '“Добровольческое объединение”', description: 'Добровольческое объединение года - Добровольческое объединение – это студенческое объединение или клуб, занимающийся развитием массового волонтерского (добровольческого) движения. Являясь участником или организатором (соорганизатором) благотворительных мероприятий, объединение не только заявило о себе на разных уровнях, но и принесло очевидную пользу обществу вне зависимости от выбранной сферы.', svg: '8.svg' },
  {id: 10, name: '“Студенческое СМИ года”', description: 'Студенческое СМИ года – Студенческое СМИ – это объединение активных и молодых медийщиков (журналистов, блогеров, фотографов и т.д.), цель которых - развивать медиапространство как своей образовательной организации, так и на более высоком уровне. Они участвуют в профессиональных конкурсах, освещают молодежные мероприятия и уже признаны в журналистском сообществе. Создавая качественный и достоверный контент, объединение участвует в развитии информационной грамотности в молодежной среде – за что высоко оценены в сообществе.', svg: '9.svg' }
